  margin: 5px;
  color: white;
}
#mail:hover {
  color: rgb(233, 88, 59);
}

div svg:hover {
  -webkit-transform: scale(1.25);
          transform: scale(1.25); 
          color: rgb(92, 139, 204);
  
}
